import React,{ useRef } from 'react'
import { useFormik } from 'formik'
import axios from 'axios'
import { API } from '../../util/API'
import { useNavigate } from 'react-router-dom'

const Image = () => {
 
  let navigate = useNavigate();
  let file = useRef();
  let{ handleChange,handleSubmit} = useFormik({
    initialValues :  { 
      name : "",
      image : ""
    },
    onSubmit : (formdata)=>{
    // console.log(formdata);
    // console.log(file.current.files[0])
    let myfile = file.current.files[0];

    let frm = new FormData();
    frm.append("abc", formdata.name);
    frm.append("photo", myfile);
    axios.post(`${API}/image`,frm).then(response=>{
    // console.log(response.data);
    navigate("/admin/dashboard");
    })
    }
  });

  return (
    <>
    <div className='container'>
      <div className='row'>
        <div className='col-md-12 my-4'>
          <form onSubmit={handleSubmit}>
            Image Name <input type='text' name='name' onChange={handleChange} />
            <br/>
            <br/>
            Select Image <input ref={file} type='file' name='image' onChange={handleChange} />
            <br/>
            <br/>
            <button type='submit' className='btn btn-success'>Upload</button>
          </form>
        </div>
      </div>
    </div>
    </>
  )
}

export default Image