import React,{ useState , useEffect} from 'react'
import { useFormik } from 'formik';
import {API} from '../../../util/API'
import axios from 'axios'
import { useNavigate } from 'react-router-dom';

const Signup = () => {

    let navigate = useNavigate();
    let[city,setCity] = useState([]);
    useEffect(()=>{
     axios.get(`${API}/city`).then(response=>{
        setCity(response.data);
     })
    },[]);

    let[state,setState] = useState([]);
    useEffect(()=>{
    axios.get(`${API}/city/state`).then(response=>{
    setState(response.data);
    })
    },[]);

    let{ handleChange,handleSubmit,errors,touched } = useFormik({
        initialValues :{
            fullname : "",
            username : "",
            password : "",
            repassword : "",
            address : "",
            state : "",
            city : "",     
            contact : ""      
         },
         onSubmit : (formData)=>{
         console.log(formData);
         axios.post(`${API}/user/signup`,formData).then((response)=>{
        //  console.log(response.data);
         navigate("/login");
        })
    }
    });

    let getCity = (e)=>{ 
    //  console.log(e.target.value);
    let x = e.target.value;  
    axios.get(`${API}/city/state/${x}`).then(response=>{
    // console.log(response.data);
    setCity(response.data);
    });
    }


    return (
    <>
    <div className='container my-3 p-5'  style={{minHeight : "700px"}}>
        <form onSubmit={handleSubmit}>
      <div className='row'>
        <div className='col-md-6 offset-md-3'>
            <div className='card'>
                <div className='card-header'>
                    <h4>User Registration</h4>
                </div>
                <div className='card-body'>
                    <label>Full Name</label>
                    <input type='text' name='fullname' onChange={handleChange} className='form-control'/>
                </div>

                <div className='card-body'>
                    <label>Username/Email</label>
                    <input type='text' name='username' onChange={handleChange} className='form-control'/>
                </div>

                <div className='card-body'>
                    <label>Password</label>
                    <input type='password' name='password' onChange={handleChange} className='form-control'/>
                </div>

                <div className='card-body'>
                    <label>Re-Password</label>
                    <input type='password' name='repassword' onChange={handleChange} className='form-control'/>
                </div>

                <div className='card-body'>
                    <label>Address</label>
                    <textarea className='form-control' name='address' onChange={handleChange}></textarea>
                </div>

                <div className='card-body'>
                    <label>State</label>
                    <select className='form-control' name='state' onChange={(e)=>{ handleChange(e); getCity(e)}}>
                     <option>Select</option>
                     {
                        state.map((value,index)=><option key={index}>{value}</option>)
                     }
                    </select>
                </div>

                <div className='card-body'>
                    <label>City</label>
                    <select className='form-control' name='city' onChange={handleChange}>
                        <option>Select State First</option>
                        {
                         city.map(value=><option key={value._id}>{value.name}</option>)
                        } 
                    </select>
                </div>

                <div className='card-body'>
                    <label>Contact</label>
                    <input type='text' name='contact' onChange={handleChange} className='form-control'/>
                </div>           
            </div>
            <div className='card-footer'>
                <input type='submit' className='btn btn-dark'/>
            </div>
        </div>
      </div>
      </form>
    </div>
    </>
  )
}

export default Signup