import React,{ useState,useEffect,useRef } from 'react'
import { useFormik } from 'formik'
import axios from 'axios' 
import { API } from '../../../util/API'
import { useNavigate } from 'react-router-dom'
import * as YUP from 'yup'


const addBlogSchema = YUP.object({
    title : YUP.string().required("Insert Blog Title"),
    category : YUP.string().required("Select Blog Category"),
    detail : YUP.string().required("Insert Blog Detail"),
    image : YUP.mixed().required("Insert Blog Image").test('valid-type',"This File Type Not Allowed",(value)=>{
    // console.log(value);
    // return false;
    let arr = value.name.split(".");
    // console.log(arr);
    let ext = arr[arr.length-1];
    // console.log(ext); 
    if(ext == "jpg" || ext == "png" || ext == "bmp" || ext == "jpeg" || ext == "gif" || ext == "JPG" || 
    ext == "mp4" || ext == "avi" || ext == "mpeg")
    {
     return true;
    }  
    else
    {
     return false;
    }
  }).test('valid-size','Maximum size should not be more then 50 MB',(value)=>{
   // console.log(value.size);
   if(value.size > (1024*1024*50))
   {
     return false;
   }
   else 
   {
    return true;
   }
  })
});

const AddBlog = () => {
  
let myfile = useRef();
let[cate,setCate] = useState([]);
useEffect(()=>{     
axios.get(`${API}/category`).then(response=>{
// console.log(response.data);
setCate(response.data);
});
},[]);  

    let navigate = useNavigate()
    let { handleChange,handleSubmit,errors,touched,setFieldValue } =useFormik({
        validationSchema : addBlogSchema,
        initialValues : {
            title : "",
            category : "",
            detail : "",
            image : null
        },            
        onSubmit : (formData)=>{
        // console.log(formData);
        // console.log(myfile.current.files[0]);
        let frm = new FormData()
        frm.append("image",myfile.current.files[0]);
        frm.append("formdata",JSON.stringify(formData));
        axios.post(`${API}/blogs`,frm,{
            headers : {'Authorization' : localStorage.getItem("access-token")}
        }).then(response=>{
        navigate("/blogger/my-blogs");
        });
        }
    });
  return (
    <>
    <div className='container my-5'>    
        <form onSubmit={handleSubmit}>
        <div className='row'>
            <div className='col-md-8 offset-md-2'>
                <h4>Add New Blogs</h4>
                <div className='form-group'>
                    <label>Tittle</label>
                    <input type='text' name='title' onChange={handleChange} className={'form-control '+ (errors.title && touched.title ? 'is-invalid' : '')}/>
                </div>  

                <div className='form-group'>
                    <label>Category</label>
                    <select className={'form-control '+ (errors.category && touched.category ? 'is-invalid' : '')} name='category' onChange={handleChange}>
                    <option>Select</option>
                    { cate.map((value,index)=><option key={value._id}>{value.name}</option>)}
                    </select>
                </div>

                <div className='form-group'>
                    <label>Select Your Related Image</label>
                    <input ref={myfile} type='file' name='image' onChange={(event)=>setFieldValue("image",event.target.files[0])} className={'form-control '+ (errors.image && touched.image ? 'is-invalid' : '')}/>
                </div>
                {
                    errors.image && touched.image ? <small className='text-danger'>{errors.image}</small> : ''
                }
 
                <div className='form-group'>
                    <label>Blog Details</label>
                    <textarea className={'form-control '+ (errors.detail && touched.detail ? 'is-invalid' : '')} name='detail'onChange={handleChange}></textarea>
                </div>
                <br/>
                <input type='submit' className='btn btn-primary' value="Post"></input>
            </div>
        </div>
        </form>
    </div>
    </>
  )
}

export default AddBlog